
.calc {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px;
    width: auto;
}

.calc-wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 30px;
    border: 1px solid white;
    border-radius: 100px;
}

/* Numbers List */

.numbers-list {
    display: grid;
    grid-template-columns: 150px 150px 150px;
    column-gap: 10px;
    row-gap: 30px;
    padding: 10px;
    list-style: none;
}

.numbers-list__item {
    border: 1px solid white;
    border-radius: 50%;
    height: 150px;
    align-content: center;
    font-size: 64px;
    font-weight: 700;
    background-color: grey;
    color: white;
    
}

.numbers-list__item:hover, .operators-list__item:hover {
    opacity: 0.6;
}

.numbers-list__item:active, .operators-list__item:active {
    transform: translateY(5px);
}

/* Operators List */

.operators-list {
    display: flex;
    flex-direction: column;
    list-style: none;
}

.operators-list__item {
    margin: 5px;
    border: 1px solid white;
    border-radius: 50%;
    width: 130px;
    height: 130px;
    align-content: center;
    font-size: 64px;
    font-weight: 700;
    background-color: orange;
    color: white;
}

/* Result */

.result {
    padding: 20px;
}

.result input {
    padding: 5px;
    text-align: center;
    width: 500px;
    height: 50px;
    font-size: 24px;
    background-color: white;
    color: black;
    border-radius: 20px;
}

@media (max-width: 730px) {

    .calc {
        padding: 50px 0;
    }
    
    .calc-wrapper {
        padding: 20px;
        border-radius: 70px;
    }

    /* Numbers List */

    .numbers-list {
        grid-template-columns: 100px 100px 100px;
        column-gap: 15px;
        row-gap: 15px;
        padding: 5px;
    }
    
    .numbers-list__item {
        height: 100px;
        font-size: 46px;
    }

    /* Operators List */
    
    .operators-list__item {
        margin: 5px;
        width: 80px;
        height: 80px;
        font-size: 46px;
    }

    /* Result */

    .result {
        padding: 10px;
    }

    .result input {
        width: 300px;
        height: 40px;
        font-size: 26px;
    }
}


@media (max-width: 620px) {

    .calc {
        padding: 50px 0;
    }
    
    .calc-wrapper {
        padding: 20px;
        border-radius: 70px;
    }

    /* Numbers List */

    .numbers-list {
        grid-template-columns: 50px 50px 50px;
        column-gap: 10px;
        row-gap: 10px;
    }
    
    .numbers-list__item {
        height: 50px;
        font-size: 24px;
    }

    /* Operators List */
    
    .operators-list__item {
        margin: 4px;
        width: 40px;
        height: 40px;
        font-size: 24px;
    }

    /* Result */

    .result input {
        width: 200px;
        height: 30px;
        font-size: 18px;
    }
}