*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  scroll-behavior: smooth;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-color: rgb(50, 44, 44);
}

/* General */

.container { 
  margin: 0 auto;
  padding: 20px 0;
  text-align: center;
  max-width: 1200px;

}

@media (max-width: 620px) {
  .container {
    padding: 10px 0;
  }
}