.header {
    background-color: black;
    color: white;
    border-bottom: 1px solid white;
}

.header-title {
    font-size: 48px;
    letter-spacing: 0.5px;
}

@media (max-width: 730px) {
    .header-title {
        font-size: 36px;
    }
}

@media (max-width: 620px) {
    .header-title {
        font-size: 24px;
    }
}