.footer {
    background-color: black;
    color:  white;
    margin-top: auto;
    border-top: 1px solid white;
    width: 100%;
}

.footer p {
    font-size: 24px;
    letter-spacing: 0.5px;
}

@media (max-width: 730px) {
    .footer p {
        font-size: 22px;
    }
}

@media (max-width: 620px) {
    .footer p {
        font-size: 14px;
    }
}